.auth {
  position: fixed;
  right: 0;
  top: 30vh;
  border: 1px solid #000;
  padding: 10px;
  width: 100%;
}

.rightX {
  float: right;
  cursor: pointer;
  font-size: 1.2em;
}

.auth p {
  font-weight: 400;
}

@media screen and (min-width: 500px) {
  .auth {
    right: calc(50vw - 200px);
    width: 400px;
  }
}
